import React, { Component } from "react";

import leftmamba from "../../assets/img/leftside_mamba.svg";
import medium from "../../assets/img/Medium.svg";
import defiape from "../../assets/img/pr_defiapetalk.jpg";
import cryptoirons from "../../assets/img/pr_cryptoirons.jpg";
import cryptominers from "../../assets/img/pr_cryptominers.jpg";
import cryptodiamond from "../../assets/img/pr_cryptodiamond.jpg";
import coinlaunchlounge from "../../assets/img/pr_coinlaunchlounge.jpg";
import Fade from "react-reveal/Fade";

export default class Partners extends Component {
  render() {
    return (
      <>
        <section className="container">
          <Fade right>
            <img src={leftmamba} alt="leftmamba" className="partner-mamba" />
          </Fade>
          <Fade bottom cascade>
            <section className="partner">
              <h3 className="col-12 text-center font-weight-bold sub-title">
                PARTNERS
              </h3>
              <div
                style={{
                  textAlign: "center",
                  fontFamily: "Tradewinds",
                  fontSize: "50px",
                }}
              >
                Coming Soon...
              </div>
            </section>
            <section className="release">
              <h3 className="col-12 text-center font-weight-bold sub-title">
                PRESS RELEASE
              </h3>
              <div className="mediumdiv">
                {/* <a
                  href="https://t.me/DeFiApeTalk/480277"
                  target="blank"
                  style={{ textAlign: "center" }}
                >
                  <img src={defiape} alt="medium" className="medium" />
                </a> */}
                <a
                  href="https://cryptominersco.medium.com/ama-recap-crypto-miners-x-mambapad-2a138b2df800"
                  target="blank"
                  style={{ textAlign: "center" }}
                >
                  <img src={cryptominers} alt="medium" className="medium" />
                </a>
                <a
                  href="https://medium.com/@samarthsharma99587/introduction-questions-84287327403f"
                  target="blank"
                  style={{ textAlign: "center" }}
                >
                  <img src={cryptoirons} alt="medium" className="medium" />
                </a>
                <a
                  href="https://medium.com/@rr431519/ama-recap-crypto-diamonds-mambapad-7c455e1c10ac"
                  target="blank"
                  style={{ textAlign: "center" }}
                >
                  <img src={cryptodiamond} alt="medium" className="medium" />
                </a>
                <a
                  href="https://www.youtube.com/watch?v=EFyxgnDY5E0"
                  target="blank"
                  style={{ textAlign: "center" }}
                >
                  <img src={coinlaunchlounge} alt="medium" className="medium" />
                </a>
              </div>
            </section>
          </Fade>
        </section>
        <hr style={{ borderColor: "#f1b90c", marginTop: "80px" }} />
      </>
    );
  }
}
